a.button {
   text-transform: uppercase;
   padding: 0.4rem 1.3rem;
   border-radius: 30px;
   cursor: pointer;
   outline: none;
   text-decoration: none;
   font-size: 0.8rem;
   transition: border 0.2s ease-in, color 0.2s ease-in, background 0.2s ease-in;
   border: 2px solid transparent;
}